import React from "react";
import Nav from "./Nav";
import * as styles from "../styles/layout.module.css";
import "../styles/global.css";
import Burger from "./Burger";
import Cursor from "./Cursor";
import envelope from "../images/Icons/Icon2.svg";
import { Link } from "gatsby";
import Footer from "./Footer";


export default function Layout({ children }) {

  // Window Undefined Handle
  if(typeof window === "undefined") {
    return;
  }

  // Language
  const [language, setLanguage] = React.useState('');

  let species = window.location.search.split('&').slice(-1);
  species = species[0] === '?' ? '' : species;

  React.useEffect(() => {
    if (window.localStorage.getItem('CC-Language') === null) {
      window.localStorage.setItem('CC-Language', 'tr-TR');
      window.location.search = '?tr';
    } else if (window.localStorage.getItem('CC-Language') === 'en-EU') {
      window.history.replaceState( {} , "Chef's Choice", '?en' + '&' + species);
      setLanguage('en-EU');
    } else {
      window.history.replaceState( {} , "Chef's Choice", '?tr' + '&' + species);
      setLanguage('tr-TR');
    }
  }, []);

  function handleLanguage(lang) {
    if (lang === 'en-EU') {
      setLanguage('en-EU');
      window.localStorage.setItem('CC-Language', 'en-EU');
      window.location.search = '?en' + '&' + species;
    } else {
      setLanguage('tr-TR');
      window.localStorage.setItem('CC-Language', 'tr-TR');
      window.location.search = '?tr' + '&' + species;
    }
    
  };

  

  // Nav Toggle
  const innerHeight = window.innerHeight;
  const innerWidth = window.innerWidth;

  const [navToggled, setNavToggled] = React.useState(false);

  const handleNavToggle = React.useCallback(() => {
    setNavToggled(!navToggled);
  }, [navToggled]);

  function handleNavToggle2() {
    const fade = document.getElementById("fade");
    const nav = document.getElementById("nav");

    if (!navToggled) {
      let interval = -innerWidth/2;
      let alpha = 0;
      fade.style.display = "block";
      const myInterval = setInterval(function () {
        nav.style.left = interval + "px";
        fade.style.backgroundColor = "rgba(0,0,0," + alpha/100 + ")";
        interval += 10;
        alpha += 1;
        if(interval >= 10) {
          clearInterval(myInterval);
        }
      }, 5);

    } else {
      let interval = 0;
      let alpha = 50;
      const myInterval = setInterval(function () {
        nav.style.left = interval + "px";
        fade.style.backgroundColor = "rgba(0,0,0," + alpha/100 + ")";
        interval -= 10;
        alpha -= 1;
        if(interval <= -innerWidth/2) {
          clearInterval(myInterval);
          fade.style.display = "none";
        }
      }, 5);
    }

  }


  return (  
    <div className={styles.Layout} id="layout">

        <Burger handleNavToggle={() => {handleNavToggle(); handleNavToggle2();}}/>
        
        <Nav handleNavToggle={() => {handleNavToggle(); handleNavToggle2();}}/>
        
        <div className={styles.languageContainer} id="language">
            <p className={styles.EN} onClick={() => handleLanguage('en-EU')} style={{transform:(language === 'en-EU' ? 'scale(1.33)' : 'scale(1)')}} >EN</p>
            <p className={styles.TR} onClick={() => handleLanguage('tr-TR')} style={{transform:(language === 'tr-TR' ? 'scale(1.33)' : 'scale(1)')}} >TR</p>
        </div>

        <div>
            { children }
        </div>

        <Footer/>

    </div>
  )
}
