import React from "react"
import Layout from "../../components/Layout"
import { graphql, Link } from "gatsby";
import * as styles from "../../styles/products.module.css"
import logo from "../../images/Chefs Choice Logo.png";

export default function Products({ data }) {

    // Window Undefined Handle
    if(typeof window === "undefined") {
        return;
    }

    // Language 
    const [language, setLanguage] = React.useState('');
    React.useEffect(() => {
    setLanguage(window.localStorage.getItem('CC-Language'));
    }, []);

    // Language Visibility
    React.useEffect(() => {
        document.getElementById('language').style.backgroundColor = 'rgba(68, 146, 255,0.75)';

    }, []);

    // Remove footer for this page
    React.useEffect(() => {
        document.getElementById('footer').style.display = "none";
    }, []);

    // Fetch products
    const products = data.allContentfulProduct.edges;

    // Filter states
    const [currentProducts, setCurrentProducts] = React.useState([]);

    const [search, setSearch] = React.useState(''); 

    const [cat, setCat] = React.useState(false);
    const [dog, setDog] = React.useState(false);

    const [salmon, setSalmon] = React.useState(false);
    const [lamb, setLamb] = React.useState(false);
    const [chicken, setChicken] = React.useState(false);
    const [beef, setBeef] = React.useState(false);

    const [month112, setMonth112] = React.useState(false);
    const [year110, setYear110] = React.useState(false);
    const [year11plus, setYear11Plus] = React.useState(false);

    const [grainFree, setGrainFree] = React.useState(false);
    const [lowGrain, setLowGrain] = React.useState(false);

    // Nav Cat or Dog
    React.useEffect(() => {
        if(window.location.search.slice(-3) === "cat") {
            setCat(true);
            setDog(false);
        }
        if(window.location.search.slice(-3) === "dog") {
            setDog(true);
            setCat(false);
        }
    }, [window.location.search]);

    // FILTER

    React.useEffect(() => {


        let tempProducts = [];

        // EN or TR
        if (window.localStorage.getItem('CC-Language') === 'en-EU') {
            tempProducts = products.filter(product => product.node.node_locale === 'en-EU');
        } else {
            tempProducts = products.filter(product => product.node.node_locale === 'tr-TR');
        }

        // Cat or Dog
        if (cat && !dog) {
            tempProducts = tempProducts.filter(product => product.node.for_cats === true);
            window.history.replaceState( {} , "Chef's Choice", window.location.search.split('&')[0] + '&cat');
        }
        if (dog && !cat) {
            tempProducts = tempProducts.filter(product => product.node.for_cats === false);
            window.history.replaceState( {} , "Chef's Choice", window.location.search.split('&')[0] + '&dog');
        }
 

        // Protein Source
        const proteinArray = [];
        if (salmon) {
            proteinArray.push('Salmon');
            proteinArray.push('Somon Balığı');
        } else {
            proteinArray.filter(item => item !== 'Salmon' || item !== 'Somon Balığı ')
        }
        if (lamb) {
            proteinArray.push('Lamb');
            proteinArray.push('Kuzu Eti');
        } else {
            proteinArray.filter(item => item !== 'Lamb' || item !== 'Kuzu Eti')
        }
        if (beef) {
            proteinArray.push('Beef');
            proteinArray.push('Sığır Eti');
        } else {
            proteinArray.filter(item => item !== 'Beef' || item !== 'Sığır Eti')
        }
        if (chicken) {
            proteinArray.push('Chicken');
            proteinArray.push('Tavuk Eti');
        } else {
            proteinArray.filter(item => item !== 'Chicken' || item !== 'Tavuk Eti')
        }
        
        if (proteinArray.length > 0) {
            tempProducts = tempProducts.filter(product => proteinArray.includes(product.node.proteinSource[0]));
        }

        // Age Group
        const ageArray = [];

        if (month112) {
            ageArray.push('0-12 months')
        } else {
            ageArray.filter(item => item !== '0-12 months')
        }
        if (year110) {
            ageArray.push('1-10 years')
        } else {
            ageArray.filter(item => item !== '1-10 years')
        }
        if (year11plus) {
            ageArray.push('11+')
        } else {
            ageArray.filter(item => item !== '11+')
        }

        if (ageArray.length > 0) {
            tempProducts = tempProducts.filter(product => (ageArray.includes(product.node.ageGroup[0]) || ageArray.includes(product.node.ageGroup[1])));
        }

        // Grain
        if (grainFree && !lowGrain) {
            tempProducts = tempProducts.filter(product => product.node.tahlDurumu[0] === 'Grain-Free' || product.node.tahlDurumu[0] === 'Tahılsız');
        }
        if (lowGrain && !grainFree) {
            tempProducts = tempProducts.filter(product => product.node.tahlDurumu[0] === 'Low Grain' || product.node.tahlDurumu[0] === 'Az Tahıllı');
        }

        // Search
        tempProducts = tempProducts.filter(product => product.node.name.toLowerCase().includes(search.toLowerCase()));

        
        setCurrentProducts(tempProducts);

    }, [cat, dog, salmon, lamb, beef, chicken, month112, year110, year11plus, grainFree, lowGrain, search]);

    // Clear All

    function handleClearAll() {
        setCurrentProducts(products);
        setCat(false);
        setDog(false);
        setBeef(false);
        setChicken(false);
        setLamb(false);
        setSalmon(false);
        setGrainFree(false);
        setLowGrain(false);
        setMonth112(false);
        setYear110(false);
        setYear11Plus(false);
    };


    
    return (
        <Layout>
            <div className={styles.Products} id="products">

                <img src={logo} alt="logo" className={styles.logo}/>
 
                <div className={styles.filter} id="filter">

                    <input className={styles.search} type='text' name='search' placeholder={language === 'en-EU' ? 'Search for a product here...' : 'Ürün arayın...'} value={search} onInput={(e) => setSearch(e.target.value)} autoComplete="off" />

                    <div>
                        <h3 className={styles.toggleHeading}>{language === "tr-TR" ? "TÜR" : "SPECIES"}</h3>
                        <section className={styles.catDog}>
                            <p className={styles.toggle} onClick={() => {setCat(!cat); setDog(cat);}} style={{backgroundColor: cat ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "KEDİ" : "CATS"}</p>
                            <p className={styles.toggle} onClick={() => {setDog(!dog); setCat(dog);}} style={{backgroundColor: dog ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "KÖPEK" : "DOGS"}</p>
                        </section>
                    </div>
 
                    <div>
                        <h3 className={styles.toggleHeading}>{language === "tr-TR" ? "PROTEIN KAYNAĞI" : "PROTEIN SOURCE"}</h3>
                        <section className={styles.proteinSource}>
                            <p className={styles.toggle} onClick={() => setSalmon(!salmon)} style={{backgroundColor: salmon ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "SOMON" : "SALMON"}</p>
                            <p className={styles.toggle} onClick={() => setLamb(!lamb)} style={{backgroundColor: lamb ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "KUZU" : "LAMB"}</p>
                            <p className={styles.toggle} onClick={() => setBeef(!beef)} style={{backgroundColor: beef ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "SIĞIR" : "BEEF"}</p>
                            <p className={styles.toggle} onClick={() => setChicken(!chicken)} style={{backgroundColor: chicken ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "TAVUK" : "CHICKEN"}</p>                      
                        </section>
                    </div>

                    <div>
                        <h3 className={styles.toggleHeading}>{language === "tr-TR" ? "YAŞ GRUBU" : "AGE GROUP"}</h3>
                        <section className={styles.ageGroup}>
                            <p className={styles.toggle} onClick={() => setMonth112(!month112)} style={{backgroundColor: month112 ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "1-12 AYLIK" : "1-12 MONTHS"}</p>
                            <p className={styles.toggle} onClick={() => setYear110(!year110)} style={{backgroundColor: year110 ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "1-10 YAŞ" : "1-10 YEARS"}</p>
                            <p className={styles.toggle} onClick={() => setYear11Plus(!year11plus)} style={{backgroundColor: year11plus ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "11 YAŞ ÜSTÜ" : "11+ YEARS"}</p>                     
                        </section>
                    </div>

                    <div>
                        <h3 className={styles.toggleHeading}>{language === "tr-TR" ? "TAHIL MİKTARI" : "GRAIN AMOUNT"}</h3>
                        <section className={styles.grain}>
                            <p className={styles.toggle} onClick={() => setGrainFree(!grainFree)} style={{backgroundColor: grainFree ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "TAHILSIZ" : "GRAIN-FREE"}</p>
                            <p className={styles.toggle} onClick={() => setLowGrain(!lowGrain)} style={{backgroundColor: lowGrain ? 'rgba(68, 146, 255, 1)' : 'rgba(68, 146, 255, 0.5)'}}>{language === "tr-TR" ? "AZ TAHILLI" : "LOW GRAIN"}</p>
                        </section>
                    </div>

                    <p className={styles.clearAll} onClick={handleClearAll}>{language === "tr-TR" ? "TEMİZLE" : "CLEAR ALL"}</p>

               </div>

                <div className={styles.productsContainer} id="productsContainer">
                    {
                        currentProducts.map(product =>
                            <Link to={product.node.slug} key={product.node.id} className={styles.productLink}>
                                <img src={product.node.packagePhoto.url} alt="product" className={styles.productPhoto}/>
                                <p>{product.node.name}</p>
                                <p className={styles.adulthood}>{product.node.ageGroup[0] === '0-12 months' ? (product.node.for_cats ? (language === "tr-TR" ? "Yavru Kediler" : "Kittens") : (language === "tr-TR" ? "Yavru Köpekler" : "Puppies")) : (language === "tr-TR" ? "Yetişkin" : "Adult")}</p>
                            </Link>
                        )
                    }
                </div>

            </div>
        </Layout>
    )
}

export const query = graphql`
    query ProductsPage {
        allContentfulProduct(filter: {brand: {brandName: {eq: "Chef's Choice"}}}) {
            edges {
                node {
                    id
                    name
                    slug
                    packagePhoto {
                        url
                    }
                    node_locale
                    for_cats
                    proteinSource
                    ageGroup
                    tahlDurumu
                }
            }
        }
    }
`